
















































import { Component, Prop, Vue } from 'vue-property-decorator';

import team from '@/../public/json/team.json';

import ArticleParagraph from '@/components/ArticleParagraph.vue';

@Component({
    components: {
        ArticleParagraph
    }
})
export default class Team extends Vue {
    private team = team;

    private getTeamImg (name: string): string {
        switch (name) {
            case '':
                return 'gen';
            case '':
                return 'admin';
            case '':
                return 'agenda';
            case '':
                return 'supage';
            case '':
                return 'pr';
            case '':
                return 'service';
            case '':
                return 'design';
            case '':
                return 'fin';
            case '':
                return 'dev';
            case '':
                return 'edit';
            case '':
                return 'photo';
            default:
                return 'dev';
        }
    }
}
